<template>

  <body class="income">
    <van-nav-bar fixed title="收入明细" left-text="">
      <van-icon @click="onClickLeft" name="arrow-left" slot="left" />
    </van-nav-bar>
    <van-cell-group>
      <van-cell value="" style="color:#5aa9e7;" icon="tosend" is-link>
        <template slot="title">
          <span class="van-cell-text">商城收入明细</span>
        </template>
      </van-cell>
    </van-cell-group>
    <van-cell-group>
      <van-cell value="" style="color:#5aa9e7;" icon="tosend" is-link>
        <template slot="title">
          <span class="van-cell-text">推广收入明细</span>
        </template>
      </van-cell>
    </van-cell-group>
    <van-cell-group>
      <van-cell value="" style="color:#b823ab;" icon="value-card" is-link>
        <template slot="title">
          <span class="van-cell-text">贷款收入明细</span>
        </template>
      </van-cell>
    </van-cell-group>
    <van-cell-group>
      <van-cell value="" style="color:#b823ab;" icon="value-card" is-link>
        <template slot="title">
          <span class="van-cell-text">网贷能手明细</span>
        </template>
      </van-cell>
    </van-cell-group>

    <footer-bot></footer-bot>
  </body>
</template>

<script>
import FooterBot from '@/components/common/FooterBot'
export default {
  name: 'app',
  data() {
    return {
      active: 1
    }
  },
  components: {
    FooterBot
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
.income {
  background: #fff;
  margin-top: 46px;
}
.income .van-cell-text  {
  color: black;
}
.income .van-nav-bar {
  color: #fff;
  background-color: #39bea9;
}
.income .van-nav-bar .van-icon {
  color: #fff;
}
</style>


