import Vue from 'vue'
import Router from 'vue-router'

import index from '@/components/index'
import recommend from '@/components/recommend'
import personal from '@/components/personal'
import share from '@/components/share'
import chat from '@/components/chat'
import search from '@/components/search'
import lmustloan from '@/components/lmustloan'
import CreditCard from '@/components/CreditCard'
import member from '@/components/member'
import extension from '@/components/extension'
import MyGrade from '@/components/MyGrade'
import MyInformation from '@/components/MyInformation'
import PresentRecord from '@/components/PresentRecord'
import IncomeDetails from '@/components/IncomeDetails'
import about from '@/components/about'
import login from '@/components/login'
import register from '@/components/register'
import ForgetPassword from '@/components/ForgetPassword'
import details from '@/components/details'
import sell from '@/components/sell'




Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
    {path: '/',name: 'index',component: index},
    {path: '/personal',name: 'personal',component: personal},
    {path: '/share',name: 'share',component: share},
    {path: '/chat',name: 'chat',component: chat,
      // beforeEnter:(to,from,next)=>{
      //   alert('请登录');
      //   next();
      //     }
      },
    {path: '/search',name: 'search',component: search},
    {path: '/lmustloan',name: 'lmustloan',component: lmustloan},
    {path: '/CreditCard',name: 'CreditCard',component: CreditCard},
    {path: '/member',name: 'member',component: member},
    {path: '/extension',name: 'extension',component: extension},
    {path: '/MyGrade',name: 'MyGrade',component: MyGrade},
    {path: '/MyInformation',name: 'MyInformation',component: MyInformation},
    {path: '/PresentRecord',name: 'PresentRecord',component: PresentRecord},
    {path: '/IncomeDetails',name: 'IncomeDetails',component: IncomeDetails},
    {path: '/about',name: 'about',component: about},
    {path: '/login',name: 'login',component: login},
    {path: '/register',name: 'register',component: register},
    {path: '/ForgetPassword',name: 'ForgetPassword',component: ForgetPassword},
    {path: '/details',name: 'details',component: details},
    {path: '/sell',name: 'sell',component: sell},
    {path: '/recommend',name: 'recommend',component: recommend},
    
  ]
})





