<template>
    <body class="search">
        <van-search
            v-model="value"
            show-action
            @search="onSearch"
            >
            <div slot="action" @click="onSearch">搜索</div>
        </van-search>
    

        <footer-bot></footer-bot>
    </body>
</template>

<script>
import FooterBot from "@/components/common/FooterBot";
export default {

  name: "app",
  data() {
    return {

    };
  },
  components: {
    FooterBot
  },

};
</script>

<style scoped>
.search .van-search--show-action {
    padding-right: 1rem;
}
.search .van-search {
    padding: 1rem 15px;
}
.search .van-search__action {
  margin-left: 1% !important;
}
</style>



