<template>

  <body class="details">
    <van-nav-bar fixed title="详细" left-text="">
      <van-icon @click="onClickLeft" name="arrow-left" slot="left" />
    </van-nav-bar>


    <footer-bot></footer-bot>
  </body>
</template>

<script>
import FooterBot from '@/components/common/FooterBot'
export default {
  name: 'app',
  data() {
    return {
    }
  },
  components: {
    FooterBot
  },
  methods: {
    onClickLeft() {
      this.$router.push({ name: 'index' })
    }
  }
}
</script>

<style scoped>
.details {
  background: #fff;
  margin-top: 46px;
}
.details .van-cell-text  {
  color: black;
}
.details .van-nav-bar {
  color: #fff;
  background-color: #39bea9;
}
.details .van-nav-bar .van-icon {
  color: #fff;
}
</style>


