<template>
  <body class="sell">
    <van-nav-bar fixed title="商城" left-text="">
      <van-icon @click="onClickLeft" name="arrow-left" slot="left" />
    </van-nav-bar>
    <footer-bot></footer-bot>
  </body>
</template>

<script>
import FooterBot from '@/components/common/FooterBot'
export default {
  name: 'app',
  data() {
    return {
      active: 1
    }
  },
  components: {
    FooterBot
  },
  methods: {
    onClickLeft() {
      this.$router.push({ name: 'index' })
    }
  }
}
</script>

<style scoped>
.sell {
  background: #fff;
  margin-top: 46px;
}
.sell .van-cell-text  {
  color: black;
}
.sell .van-nav-bar {
  color: #fff;
  background-color: #39bea9;
}
.sell .van-nav-bar .van-icon {
  color: #fff;
}
[class*=van-hairline]::after {
  content: '';
  border: 0 solid #38bea8 !important;
}
</style>


