<template>
    <footer class="footer">
      <van-tabbar v-model="active">
        <van-tabbar-item :to="homeLink" icon="shop">首页</van-tabbar-item>
        <van-tabbar-item to="share" icon="share" info="new" >分享</van-tabbar-item>
        <van-tabbar-item :to="{name:'chat'}" icon="chat" info="5">消息</van-tabbar-item>
        <van-tabbar-item :to="{name:'personal'}" icon="contact" >个人中心</van-tabbar-item>
      </van-tabbar>
    </footer>
</template>

<script>
export default {
  data() {
    return {
      active: 0,
      homeLink:'/'
    }
  },

};
</script>

<style scoped>
.van-tabbar-item--active {
  color: #39bea9 !important;
}
.footer {
  z-index: 99;
}
</style>



