<template>
  <body class="login">
    <van-nav-bar fixed title="登陆" left-arrow @click-left="onClickLeft">
    </van-nav-bar>
    <div>
      <van-cell-group>
  <van-field
    v-model="username"
    label="用户名"
    icon="clear"
    placeholder="请输入手机号"
    required
    @click-icon="username = ''"
  />

  <van-field
    v-model="password"
    type="password"
    label="密码"
    placeholder="请输入密码"
    required
  />
</van-cell-group>
      
    </div>
      <van-button type="primary" bottom-action>登陆</van-button>
      <p>
        <router-link to="register" tag="span">注册</router-link><router-link to="ForgetPassword" tag="span">忘记密码</router-link>
      </p>
    <footer-bot></footer-bot>
  </body>
</template>

<script>
import FooterBot from '@/components/common/FooterBot'
export default {
  name: 'app',
  data() {
    return {
      password: '',
      username:'',
    }
  },
  components: {
    FooterBot
  },

  methods: {
    onClickLeft() {
      this.$router.push({name: 'index'});
    },
  }
}
</script>

<style scoped>
html {
    background: #fff !important;
}
.login {
  padding: 46px 1rem 1rem 1rem;
}
.login p {
  display: flex;
  justify-content: space-between;
}


.login .van-button--bottom-action.van-button--primary {
    background-color: #39bea9;
    margin: 1rem 0;
}

</style>



