<template>
  <body class="record">
    <van-nav-bar fixed title="已提金额" left-arrow @click-left="onClickLeft">
    </van-nav-bar>

    <footer-bot></footer-bot>
  </body>
</template>

<script>
import FooterBot from '@/components/common/FooterBot'
export default {
  name: 'app',
  data() {
    return {

    }
  },
  components: {
    FooterBot
  },

  methods: {
    onClickLeft() {
      this.$router.go(-1)
    },
  }
}
</script>

<style scoped>
.record {
  margin-top: 46px;
}
</style>



